/* MainPage.module.css - 메인 페이지 커스텀 스타일 */

.container {
  min-height: 100vh;
  background-color: #f9fafb;
}

.content {
  padding: 24px 0;
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 16px;
}

@media (min-width: 640px) {
  .wrapper {
    padding: 0 24px;
  }
}

@media (min-width: 1024px) {
  .wrapper {
    padding: 0 32px;
  }
}